.App {
  display: flex;
  justify-content: center;
}
.form-border {
  background-color:gainsboro;
  
  padding: 10px;
  margin-top:50px;
  text-align: center;
  border: 2px solid black;
  border-radius: 5px;
}

label {
  font-weight: bold;
  display: flex;
  margin-bottom: 5px;
  
}

input[type='text'],
input[type='email']
 {
  display: block;
  width: 400px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px; 
}

.form-control {
  margin-bottom: 20px;
}

.error {
  color: red;
}

#submitBtn{
  
  background-color: grey;
  border:1px solid black;
  border-radius: 5px;
  padding: 7px;
  font-size:small;
}